import { isObject } from '@guardian/libs';
export const ALL_USER_TYPES = ['new', 'guest', 'current'];
export const isUserType = (value) => ALL_USER_TYPES.includes(value);
export const ALL_PRODUCTS = [
    'Contribution',
    'SupporterPlus',
    'Paper',
    'GuardianWeekly',
];
export const isProduct = (value) => ALL_PRODUCTS.includes(value);
export function isCheckoutCompleteCookieData(obj) {
    return isObject(obj) && isUserType(obj.userType) && isProduct(obj.product);
}
